            <div className="main-maket-text">–û—Ñ–∏—Ü–∏–∞–ª—å–Ω–∞—è –ø—Ä–æ–¥—É–∫—Ü–∏—è —Å –≥–∞—Ä–∞–Ω—Ç–∏–µ–π 12 –º–µ—Å—è—Ü–µ–≤, –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏–∏, –ø–æ–¥–¥–µ—Ä–∂–∫–∞, –≥–∏–±–∫–∏–µ —É—Å–ª–æ–≤–∏—è –¥–ª—è –æ–ø—Ç–æ–≤–∏–∫–æ–≤ –∏ –∫–æ–º–ø–∞–Ω–∏–π. –í –Ω–∞—à–µ–º –∫–∞—Ç–∞–ª–æ–≥–µ —à–∏—Ä–æ–∫–∏–π –≤—ã–±–æ—Ä –¥—Ä–µ–ª–∏, –ø–µ—Ä—Ñ–æ—Ä–∞—Ç–æ—Ä—ã, –±–æ–ª–≥–∞—Ä–∫–∏, —à—É—Ä—É–ø–æ–≤—ë—Ä—Ç—ã, –ø–∏–ª—ã, —à–ª–∏—Ñ–º–∞—à–∏–Ω—ã, –∏–∑–º–µ—Ä–∏—Ç–µ–ª—å–Ω—ã–µ –ø—Ä–∏–±–æ—Ä—ã –∏ –∫–æ–º–ø–ª–µ–∫—Ç—É—é—â–∏–µ. –ü–æ—Å—Ç–∞–≤–ª—è–µ–º —ç–ª–µ–∫—Ç—Ä–æ–∏–Ω—Å—Ç—Ä—É–º–µ–Ω—Ç –¥–ª—è —á–∞—Å—Ç–Ω—ã—Ö –∫–ª–∏–µ–Ω—Ç–æ–≤, —Å—Ç—Ä–æ–∏—Ç–µ–ª—å–Ω—ã—Ö –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–π, —Å–Ω–∞–±–∂–µ–Ω—Ü–µ–≤ –∏ —Ç–µ–Ω–¥–µ—Ä–Ω—ã—Ö –∑–∞–∫—É–ø–æ–∫.</div>
